import React, { Component } from 'react';
import Navbar from '../shared/Navbar';
import Sidebar from '../shared/Sidebar';
import Footer from '../shared/Footer';
import { Link } from 'react-router-dom';
import { Form } from 'react-bootstrap';
import axios from 'axios'
import CKEditor from 'ckeditor4-react';
import { TurnedIn } from '@material-ui/icons';

class CreatePost extends Component {
    state = {
        title: '',
        meta_name: '',
        meta_content: '',
        post_heading: '',
        error: '',
        selectedFile: null,
        meta_description: '',
        id_proof: '',
        isLoading : true,
        pageValue : undefined,

    }

    constructor(props) {
        super(props);
     
        this.state = {
            data: this.state.data,
            posts : [],

        };

        this.handleChange = this.handleChange.bind(this);
        this.onEditorChange = this.onEditorChange.bind(this);
    }


    onEditorChange(evt) {
        this.setState({
            data: evt.editor.getData()
        });
    }

    handleChange(changeEvent) {
        this.setState({
            data: changeEvent.target.value
        });
    }

    onChange = e => {
        this.setState({
            id_proof: e.target.files[0]
        })
    }


    redClass = {
        'color': 'red'
    }


    addFormData(e) {
        if (!this.state.title) {
            return this.setState({ error: 'Title is required' });
        }

        if (!this.state.meta_name) {
            return this.setState({ error: 'Meta Name is required' });
        }

        if (!this.state.meta_content) {
            return this.setState({ error: 'Meta Content is required' });
        }

        if (!this.state.post_heading) {
            return this.setState({ error: 'Post heading is required' });
        }

        if (!this.state.id_proof) {
            return this.setState({ error: 'Post Image is required' });
        }

        if (!this.state.data) {
            return this.setState({ error: 'Post Description is required' });
        }

        this.setState({ error: '' });

        e.preventDefault();
        const formData = new FormData();
        formData.append('title', this.state.title)
        formData.append('meta_name', this.state.meta_name)
        formData.append('meta_content', this.state.meta_content)
        formData.append('post_heading', this.state.post_heading)
        formData.append('meta_desc', this.state.data)
        formData.append('id_proof', this.state.id_proof)


        var headers = {
            'Content-Type': 'application/json',
            "Access-Control-Allow-Origin": "*"
        }

        axios.post('https://jovialconnection.com/autopostApi/savepost.php', formData, headers)
            .then(function (response) {
                console.log(response);
                console.log(response.data);
                if (response.data.success == true) {
                    this.setState({ error: 'Post Addedd Successfully' });
                    window.location = "/basic-ui/dropdowns"

                }
            })
            .catch(function (response) {
                window.location = "/createpost"
            });
    }

    render() {
                console.log(this.props.location.pageValue);

        return (<div>
            <div className="proBanner">
            </div>
            <div className="row">
                <div className="col-xl-3 col-sm-6 grid-margin stretch-card">
                    <div className="card">
                        <div className="card-body">
                            <div className="row">
                                <div className="col-9">
                                    <div className="d-flex align-items-center align-self-start">

                                    </div>
                                </div>
                                <div className="col-3">
                                    <div className="icon icon-box-success ">
                                        <span className="mdi mdi-arrow-top-right icon-item"></span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-xl-3 col-sm-6 grid-margin stretch-card">
                    <div className="card">
                        <div className="card-body">
                            <div className="row">
                                <div className="col-9">
                                    <div className="d-flex align-items-center align-self-start">

                                    </div>
                                </div>
                                <div className="col-3">
                                    <div className="icon icon-box-success">
                                        <span className="mdi mdi-arrow-top-right icon-item"></span>
                                    </div>
                                </div>
                            </div>


                        </div>
                    </div>
                </div>
                <div className="col-xl-3 col-sm-6 grid-margin stretch-card">
                    <div className="card">
                        <div className="card-body">
                            <div className="row">
                                <div className="col-9">
                                    <div className="d-flex align-items-center align-self-start">
                                        <h3 className="mb-0">$12.34</h3>
                                        <p className="text-danger ml-2 mb-0 font-weight-medium">-2.4%</p>
                                    </div>
                                </div>
                                <div className="col-3">
                                    <div className="icon icon-box-danger">
                                        <span className="mdi mdi-arrow-bottom-left icon-item"></span>
                                    </div>
                                </div>
                            </div>
                            <h6 className="text-muted font-weight-normal">Daily Income</h6>
                        </div>
                    </div>
                </div>
                <div className="col-xl-3 col-sm-6 grid-margin stretch-card">
                    <div className="card">
                        <div className="card-body">
                            <div className="row">
                                <div className="col-9">
                                    <div className="d-flex align-items-center align-self-start">
                                        <h3 className="mb-0">$31.53</h3>
                                        <p className="text-success ml-2 mb-0 font-weight-medium">+3.5%</p>
                                    </div>
                                </div>
                                <div className="col-3">
                                    <div className="icon icon-box-success ">
                                        <span className="mdi mdi-arrow-top-right icon-item"></span>
                                    </div>
                                </div>
                            </div>
                            <h6 className="text-muted font-weight-normal">Expense current</h6>
                        </div>
                    </div>
                </div>
            </div>

            <div className="row">
                <div className="col-12 grid-margin stretch-card">
                    <div className="card">
                        <div className="card-body">
                            <p style={this.redClass} >{this.state.error}</p>
                            <form className="forms-sample">
                                <Form.Group>
                                    <label htmlFor="exampleInputName1">Post Title </label>
                                    <textarea onChange={e => this.setState({ title: e.target.value })} className="form-control" id="exampleTextarea1" rows="4" placeholder="Title"></textarea>

                                </Form.Group>
                                <Form.Group>
                                    <label htmlFor="exampleInputEmail3">Post Name</label>
                                    <textarea onChange={e => this.setState({ meta_name: e.target.value })} className="form-control" rows="4" placeholder="Meta  Name" ></textarea>
                                </Form.Group>
                                <Form.Group>
                                    <label htmlFor="exampleInputPassword4">Post Content</label>
                                    <textarea className="form-control" rows="4" onChange={e => this.setState({ meta_content: e.target.value })} id="exampleInputPassword4" placeholder="Meta Content" ></textarea>
                                </Form.Group>
                                <Form.Group>
                                    <label htmlFor="exampleSelectGender">Post Heading</label>
                                    <textarea className="form-control" rows="4" onChange={e => this.setState({ post_heading: e.target.value })} placeholder="Post Heading" ></textarea>

                                </Form.Group>

                                <Form.Group>
                                    <label>Post Image</label>
                                    <div className="custom-file">
                                        <Form.Control type="file" onChange={this.onChange} accept=".jpg,.jpeg,.png" name="id_proof" className="form-control visibility-hidden" id="customFileLang" lang="es" />
                                        <label className="custom-file-label" htmlFor="customFileLang">Upload image</label>
                                    </div>
                                </Form.Group>

                                <label htmlFor="exampleSelectGender">Meta Description</label>
                                <div style={{ 'borderBlockStyle': 'unset', 'border': 'outset', 'borderWidth': '1px' }} className="form-group">
                                    <CKEditor data={this.state.data} onChange={this.onEditorChange} />
                                </div>
                                <button onClick={e => this.addFormData(e)} type="button" className="btn btn-primary mr-2">Save Button</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>);

    }


}


export default CreatePost;